import React, { useState, useEffect } from "react";
import { Button, Col, Row, List } from "antd";
import { Card, Image } from "antd";
import { FloatButton, Modal, Alert } from "antd";
import { Typography } from "antd";
// import './QuestionPage.module.css';
import Markdown from "./Markdown";

import { SingleChoice, MultipleChoice } from "./QuestionChoice";

import {
  QuestionCircleOutlined,
  LeftOutlined,
  RightOutlined,
} from "@ant-design/icons";

import QRScanner from "./QRScanner";

const { Text } = Typography;

function checkListsEqual(list1, list2) {
  if (list1.length !== list2.length) {
    return false;
  }
  
  const sortedList1 = list1.sort();
  const sortedList2 = list2.sort();
  
  for (let i = 0; i < sortedList1.length; i++) {
    if (sortedList1[i] !== sortedList2[i]) {
      return false;
    }
  }
  
  return true;
}


export default function QuestionPage({
  questions,
  setQuestions,
  questionIndex,
  totalQuestions,
}) {
  const [showTip, setShowTip] = useState(false);
  const [image, setImage] = useState(null);

  // pc, mobile 适配
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  // const handleResize = () => {
  //   setWindowWidth(window.innerWidth);
  // };
  // window.addEventListener("resize", handleResize);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };
    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const {
    question,
    type,
    url,
    tip,
    answeredCorrectly,
    userAnswer,
  } = questions[questionIndex];
  console.log("question:", questions[questionIndex]);

  let { options, answer } = questions[questionIndex]
  if (type === "single_choice") {
    options = JSON.parse(options)
  } else if (type === "multi_choice") {
    options = JSON.parse(options)
    answer = JSON.parse(answer)
  }

  // 处理“显示提示”按钮点击事件的函数
  function handleShowTipClick() {
    setShowTip(!showTip);
  }

  function handleCheckAnswer(userAnswer, image) {
    console.log("handleCheckAnswer", userAnswer, image)
    if (image) setImage(image);

    let answeredCorrectly = false
    if (type === "multi_choice") {
      answeredCorrectly = checkListsEqual(answer, userAnswer)
    } else {
      answeredCorrectly = userAnswer === answer;
    }
    const newQuestions = [...questions];
    newQuestions[questionIndex] = {
      ...newQuestions[questionIndex],
      answeredCorrectly,
      // answeredWrongly: !answeredCorrectly,
      userAnswer: userAnswer,
    };
    // Update the questions array with the new data
    setQuestions(newQuestions);

    // 更新用户答案缓存
    const answerCache = {};

    newQuestions.forEach(({ id, answeredCorrectly }) => {
      // console.log("answeredCorrectly=====", answeredCorrectly);
      if (answeredCorrectly !== undefined) {
        answerCache[id] = answeredCorrectly;
      }
    });
    localStorage.setItem("answerCache", JSON.stringify(answerCache));
  }



  const handleOk = () => {
    setShowTip(false);
  };
  const handleCancel = () => {
    setShowTip(false);
  };


  return (
    <div style={{ position: "relative", minHeight: "80vh" }}>
      {windowWidth > 768 ? (
        <Row gutter={16}>
          <Col span={12}>
            <Card
              title="题目"
              extra={
                <Text>
                  {questionIndex + 1}/{totalQuestions}
                </Text>
              }
            >
              <Markdown doc={question}></Markdown>
              {url && <Image src={url} />}
            </Card>
          </Col>

          <Col span={12}>
            <Card title="作答">
                <>
                  {type === "single_choice" && (
                    <SingleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e.target.value)}
                    />
                  )}

                  {type === "multi_choice" && (
                    <MultipleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e)}
                    />
                  )}

                  {type === "scan" && (
                    <QRScanner
                      key={questionIndex}
                      onAnswer={handleCheckAnswer}
                    />
                  )}

                  <Row justify="center">
                    {answeredCorrectly === true &&
                      <Alert message="恭喜，回答正确" type="success" />
                    }
                    {answeredCorrectly === false &&
                      <Alert message="回答错误, 请重新作答" type="error" />
                    }
                    {image && <Image src={image} style={{ width: "100%" }} />}
                  </Row>
                </>
            </Card>
          </Col>
        </Row>
      ) : (
        <>
          <div style={{ width: "100%", height: "80vh", overflow: "auto" }}>
            <Card
              title="题目"
              extra={
                <Text>
                  {questionIndex + 1}/{totalQuestions}
                </Text>
              }
            >
              <Markdown doc={question}></Markdown>
              {url && <Image src={url} />}
            </Card>

            <Card title="作答">

                <>
                  {type === "single_choice" && (
                    <SingleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e.target.value)}
                    />
                  )}

                  {type === "multi_choice" && (
                    <MultipleChoice
                      options={options}
                      value={userAnswer}
                      onChange={(e) => handleCheckAnswer(e)}
                    />
                  )}

                  {type === "scan" && (
                    <QRScanner
                      key={questionIndex}
                      onAnswer={handleCheckAnswer}
                    />
                  )}

                  <Row justify="center" gutter={[0, 16]}>
                    {answeredCorrectly === true &&
                      <Alert message="恭喜，回答正确" type="success" />
                    }
                    {answeredCorrectly === false &&
                      <Alert message="回答错误, 请重新作答" type="error" />
                    }
                    {image && <Image src={image} style={{ width: "100%" }} />}
                  </Row>
                </>
            </Card>
          </div>

          {/* <input
            type="text"
            onChange={handleUserAnswerChange}
            value={userAnswer}
          /> */}
        </>
      )}



      <FloatButton.Group shape="circle" style={{ right: 24 }}>
        <FloatButton
          icon={<QuestionCircleOutlined />}
          onClick={handleShowTipClick}
        />
        {/* <FloatButton /> */}
        {/* <FloatButton.BackTop visibilityHeight={0} /> */}
      </FloatButton.Group>

      <Modal
        title="提示"
        open={showTip}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <p>{tip}</p>
      </Modal>

 
    </div>
  );
}
