import { useState, useRef, useEffect } from "react";
import { Row, Col, Radio, Button, Upload } from "antd";
import { UploadOutlined } from "@ant-design/icons";

import QrScanner from "react-qr-scanner";
import { QrReader } from "react-qr-reader";
import jsQR from "jsqr";

function QRScanner({ onAnswer }) {
  const [scannerType, setScannerType] = useState("file");

  const fileInputRef = useRef(null);
  // const [result, setResult] = useState("");
  // const [image, setImage] = useState(null);
  // const qrScannerRef = useRef(null);

  // const [method, setMethod] = useState("camera");
  // const handleMethodChange = (e) => {
  //   setMethod(e.target.value);
  // };

  // useEffect(() => {
  //   // Access the user's camera and stream the video to the videoRef
  //   navigator.mediaDevices.getUserMedia({ video: true })
  //     .then(stream => {
  //       if (videoRef.current) {
  //         videoRef.current.srcObject = stream;
  //       }
  //     })
  //     .catch(err => console.error(err));
  // }, []);

  function handleButtonClick() {
    // Trigger the file input dialog
    fileInputRef.current.click();
  }

  function handleFileInputChange(event) {
    const file = event.target.files[0];
    if (!file) {
      return;
    }

    var reader = new FileReader();
    reader.onload = function () {
      // setImage(reader.result);

      var img = new Image();
      img.onload = function () {
        var canvas = document.createElement("canvas");
        canvas.width = img.width;
        canvas.height = img.height;
        var ctx = canvas.getContext("2d");
        ctx.drawImage(img, 0, 0);
        var imageData = ctx.getImageData(0, 0, img.width, img.height);
        const parsed = jsQR(imageData.data, imageData.width, imageData.height);
        console.log("result", parsed);
        if (parsed) {
          // setResult(parsed.data);
          onAnswer(parsed.data, reader.result);
        } else {
          // setResult("未识别出二维码");
          onAnswer("", reader.result);
        }
      };
      img.src = reader.result;
    };
    reader.readAsDataURL(file);
  }

  const handleScan = async (data) => {
    if (data) {
      console.log("scan:", data);
      // setResult(data.text);

      const url = data.canvas.toDataURL();
      console.log("url:", url);
      // setImage(url);

      onAnswer(data.text, url);
    }
  };

  const handleError = (err) => {
    console.error(err);
  };

  return (
    <>
      <Col style={{ paddingBottom: "20px" }}>
        <Row justify="center" style={{ paddingBottom: "20px" }}>
          <Radio.Group
            value={scannerType}
            onChange={(e) => setScannerType(e.target.value)}
          >
            <Radio.Button value="camera">拍照</Radio.Button>
            <Radio.Button value="file">相册</Radio.Button>
          </Radio.Group>
        </Row>

        <Row justify="center">
          {scannerType === "camera" ? (
            <QrScanner
              onScan={handleScan}
              onError={handleError}
              // style={{ display: "none" }}
              style={{ height: "30vh" }}
            />
          ) : (
            <>
              <Button className="upload-button" onClick={handleButtonClick}>
                选择图片
              </Button>
              <input
                type="file"
                ref={fileInputRef}
                accept="image/*"
                onChange={handleFileInputChange}
                style={{ display: "none" }}
              />
            </>
          )}
        </Row>
      </Col>
    </>
  );
}

export default QRScanner;
