import { Radio, Checkbox, Row, Col } from "antd";

// 单选题组件
export function SingleChoice({ options, value, onChange }) {
  return (
    <Radio.Group onChange={onChange} value={value}
    style={{ display: "flex", flexDirection: "column", marginInlineStart: "0px"}}
    >
        {options.map((option) => (
          <Radio key={option.id} value={option.id}
          style={{ marginBottom: "10px", textAlign: "left" }}
          >
            {option.text}
          </Radio>
        ))}
    </Radio.Group>
  );
}

// 多选题组件
export function MultipleChoice({ options, value, onChange }) {
  function handleChange(checkedValues) {
    // console.log("MultipleChoice", checkedValues)
    onChange(checkedValues.sort());
  }

  return (

    <Checkbox.Group onChange={handleChange} value={value}
    style={{ display: "flex", flexDirection: "column"}}
    >
        
        {options.map((option) => (
            <Checkbox
              key={option.id}
              value={option.id}
              style={{ marginBottom: "10px", textAlign: "left", marginInlineStart: "0px"}}
            >
              {option.text}
            </Checkbox>
          ))}
        
      </Checkbox.Group>
  );
}
