import React, { useEffect, useState } from "react";
import { useMatch, useNavigate } from "react-router-dom";
import { Button, Row, Modal, List } from "antd";
import { LeftOutlined, RightOutlined } from "@ant-design/icons";

import Slider from "react-slick";

import QuestionPage from "./QuestionPage";
import CardSlider from "./CardSlider";
import { get_questions } from "../request/api.js";

// 定义题目列表
// type: 问题类型
// url: 题目配图
// answer: 期望答案
// tip: 题目提示信息
// userAnswer: 用户提交答案
const data = [
  {
    id: 1,
    type: "scan",
    question: "问题1",
    url: "https://th.bing.com/th/id/R.073999234c22456b10598e6d13f1d01b?rik=7Ii2m7xlaLD4JA&pid=ImgRaw&r=0",
    answer: "https://qr.alipay.com/fkx144638tgu1rcd5xsdvbf",
    tip: "提示1",
    userAnswer: "",
  },
  {
    id: 2,
    question: "问题2",
    url: "https://th.bing.com/th/id/R.05b56caa3ab042a14ddb19d79049d742?rik=U7XpQ7zIzDpt5A&pid=ImgRaw&r=0",
    answer: "答案2",
    tip: "提示2",
    userAnswer: "",
  },
  // ...
  {
    id: 10,
    question: "问题10",
    answer: "答案10",
    tip: "提示10",
    userAnswer: "",
  },
];

export default function Questionnaire() {
  const match = useMatch("/scan/:id");
  const navigate = useNavigate();
  const [showCard, setShowCard] = useState(false);
  const [questions, setQuestions] = useState([]);

  console.log("Questionnaire", match);
  const cardIndex = match !== null ? parseInt(match.params["id"]) : 0
  console.log("Questionnaire cardIndex", cardIndex);

  useEffect(() => {
    get_questions()
      .then((data) => {
        const answerCacheStr = localStorage.getItem("answerCache");
        const answerCache = answerCacheStr ? JSON.parse(answerCacheStr) : {};

        data.forEach((item) => {
          const { id, userAnswer } = item;
          if (id in answerCache) {
            Object.assign(item, { answeredCorrectly: answerCache[id] });
          }
        });

        setQuestions(data);
      })
      .catch((error) => console.error("Error fetching data:", error));
  }, []);

  // 更新用户分数
  let score = 0;
  questions.forEach((question) => {
    if (question.answeredCorrectly) {
      score += 1;
    }
  });
  localStorage.setItem("score", score);

  console.log("cardindex:", cardIndex, questions.length)
  function handlePreviousClick() {
    if (cardIndex === 1) return
    navigate(`/scan/${cardIndex - 1}`)
  }

  function handleNextClick() {
    if (cardIndex === questions.length) return;
    navigate(`/scan/${cardIndex + 1}`)
  }

  function handleCheckClick() {
    setShowCard(true);
  }

  const handleCardOk = () => {
    setShowCard(false);
  };
  const handleCardCancel = () => {
    setShowCard(false);
  };

  function handleIndexClick(index) {
    console.log("click index", index);
    setShowCard(false);
    navigate(`/scan/${index}`)
  }

  function handleRestart() {
    setQuestions(
      questions.map(({ answeredCorrectly, userAnswer, ...rest }) => { return { ...rest, userAnswer: "" } })
    );

    localStorage.removeItem("answerCache")
  }

  const renderItem = (item, index) => {
    console.log("item, index", index, typeof item.answeredCorrectly);

    let buttonStyle = {
      backgroundColor:
        item.answeredCorrectly === undefined
          ? "gray"
          : item.answeredCorrectly
          ? "green"
          : "red",
    };
    // if (index === ) {
    //   buttonStyle = {
    //     ...buttonStyle,
    //     outlineColor: "transparent",
    //     outlineStyle: "solid",
    //     boxShadow: "0 0 0 4px #5a01a7",
    //   };
    // }

    return (
      <List.Item>
        <Button style={buttonStyle} onClick={() => handleIndexClick(index + 1)}>
          {index + 1}
        </Button>
      </List.Item>
    );
  };


  return (
    <>
      {questions.length > 0 && (
        <QuestionPage
          key={cardIndex}
          questions={questions}
          setQuestions={setQuestions}
          questionIndex={cardIndex-1}
          totalQuestions={questions.length}
        />
      )}

      <Row
        justify="center"
        align="middle"
        gutter={[16, 16]}
        style={{
          position: "fixed",
          zIndex: 9999,
          bottom: 0,
          left: 0,
          right: 0,
          height: "50px",
          backgroundColor: "white",
        }}
      >
        <Button onClick={handlePreviousClick} style={{ marginRight: "10px" }}>
          <LeftOutlined />
        </Button>
        <Button onClick={handleCheckClick} style={{ margin: "0 10px" }}>
          任务卡
        </Button>
        <Button onClick={handleNextClick} style={{ marginLeft: "10px" }}>
          <RightOutlined />
        </Button>
      </Row>


      <Modal
        title="任务卡"
        open={showCard}
        onOk={handleCardOk}
        onCancel={handleCardCancel}
        footer={null}
      >
        <List
          grid={{ gutter: 16, column: 5 }}
          dataSource={questions}
          renderItem={renderItem}
          justify="start"
        ></List>
        <Row justify="center">
          <Button type="primary" onClick={handleRestart}> 重新开始 </Button>
          </Row>
      </Modal>
    </>
  );
}
